.Toolbar {
    height: 56px;
    width: 100%;
    background-color: #703B09;
    position: fixed;
    top: 0;
    left: 0;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    align-items: center; 
    z-index: 90;
}

.Toolbar Nav {
    height: 100%;
}