.Input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}

.Label {
    font-weight: bold;
    display: block;
    padding: 8px;
}

.InputElement {
    background-color: white;
    outline: none;
    border: 1px solid #ccc; 
    padding: 6px 10px;
    font: inherit;
    display: block;
    width: 100%;
    box-sizing: border-box;
}

.InputElement:focus {
    outline: none;
    background-color: #ccc
}

