.NavItem {
    margin: 0;
    box-sizing: border-box;
    display: flex;
    height: 100%;
    align-items: center;
}

.NavItem a {
    color: white;
    padding: 16px 10px;
    height: 100%;
    box-sizing: border-box;
    display: block;
    border-bottom: 4px solid transparent;
    text-decoration: none;
}

.NavItem a:hover, 
.NavItem a:active,
.NavItem a.active {
    background-color: #8F5C2C;
    border-bottom: 4px solid #4084C8;
    color: white; 
}